.Note {

    background-color: #fff;
    width: 25%;
    border-radius: 2rem;
    margin: 5rem 1rem;
    box-shadow: 0px 4px 20px #ccc;
    display: inline-block;

}

.Note h1 {
    font-size: 1.5rem;
    padding: 2rem 2rem;
}

.Note p {
    font-size: 1.5rem;
    padding: 2rem 2rem;
    margin-top: -3rem;
    white-space: pre-wrap;
    word-wrap: break-word;


}

.Note button {
    padding: 0.9rem;
    border: none;
    background-color: #fff;
    color: #f5BA13;
    cursor: pointer;
    transform: scale(1.4);
    margin-left: 15rem;
    margin-bottom: 1rem;
}


@media screen and (max-width:480px) {
    .Note {
        background-color: #fff;
        height: 10rem;
        width: auto;
        border-radius: 2rem;
        margin: 5rem 1rem;
        box-shadow: 0px 4px 20px #ccc;
    }

}